<template>
  <div id="app">
    <Canvas />
    <!-- <Player /> -->
    <!-- <span id="h">
      <i>DstGOꦿེ</i><img style="width:60px;height:60px;" :src="backgourndStyle" :onerror="errorImg" /><i
        id="kcles">๑҉</i>
    </span> -->
    <router-view />
  </div>
</template>
<script>
  // import Player from "@/components/player/MusicPlayer";
  import Canvas from "@/components/canvas/vue-matrix-raindrop";

// https://raw.githubusercontent.com/Jeonke/PicGoPictureBed/master/img/avatar.gif
// https://gitee.com/cnnc/pic-go-picture-bed/raw/master/img/avatar.gif

  export default {
    name: "App",
    components: {
      // Player,
      Canvas
    },
    data: () => ({
      errorImg:
        'this.src="https://raw.githubusercontent.com/Jeonke/PicGoPictureBed/master/img/avatar.gif"',
    }),
    computed: {
      backgourndStyle: function () {
        let imgName = Math.floor(Math.random() * 26); //进行计算随机
        if (imgName < 10) {
          imgName = "0" + imgName;
        }
        let style =
          "https://raw.githubusercontent.com/Jeonke/PicGoPictureBed/master/img/Sitich/Sitich" +
          imgName +
          ".gif";
        return style;
      },
    },
  };
</script>
<style type="text/css">
  html {
    height: 100%;
  }

  /*background: url('./assets/bg.gif') no-repeat;*/
  /*background-size: 100% 100%;*/
  body {
    margin: 0;
    min-height: 100%;
    background: black;
  }

  /* background:
      radial-gradient(rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, .15) 30%, rgba(255, 255, 255, .3) 32%, rgba(255, 255, 255, 0) 33%) 0 0,
      radial-gradient(rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, .1) 11%, rgba(255, 255, 255, .3) 13%, rgba(255, 255, 255, 0) 14%) 0 0,
      radial-gradient(rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, .2) 17%, rgba(255, 255, 255, .43) 19%, rgba(255, 255, 255, 0) 20%) 0 110px,
