<template>
  <el-container>
    <!-- 组件不能写到template的根节点上，即每个组件只有一个根节点，这里的div就是这个template的根节点 -->
    <!--  getindex是自定义事件 -->
    <logo-select @getindex="getIndex"></logo-select>
    <form id="main" v-cloak>
      <div class="search-input">
        <!-- searchString 模型与文本域创建绑定 placeholder="输入搜索内容 &times;" -->
        <input type="text" v-model="searchString" @keyup="get($event)" @keydown.enter="search()"
          @keydown.down="selectDown()" @keydown.up.prevent="selectUp()" />
        <span class="search-reset" @click="clearInput()">
          <i class="el-icon-delete-solid"></i>
        </span>
        <button class="search-btn" @click="search()">SOU</button>
        <div class="search-select">
          <!-- transition-group也是vue2.0中的新特性,tag='ul'表示用ul包裹v-for出来的li -->
          <transition-group name="itemfade" tag="ul" mode="out-in" v-cloak>
            <li v-for="(value, index) in myData" :class="{ selectback: index == now }"
              class="search-select-option search-select-list" @mouseover="selectHover(index)"
              @click="selectClick(index)" :key="value">
              {{ value }}
            </li>
          </transition-group>
        </div>
      </div>
      <ul class="logo" v-if="searchString">
        <!-- 循环输出数据 -->
        <li v-for="(article, index) in filteredArticles" :key="index">
          <a v-bind:href="article.href">
            <img v-bind:src="article.image" :onerror="errorImg" />
          </a>
          <p calss>{{ article.title }}</p>
        </li>
      </ul>
    </form>
    <el-main class="mains">
      <template>
        <div id="clock" v-show="isShow">
          <el-button class="ishinner" @click='changeisShow' icon="el-icon-circle-close" type="text"></el-button>
          <p class="date">{{ date }}</p>
          <p class="time">{{ time }}</p>
          <p class="text">欢迎来到Towne的导航页！</p>
        </div>
      </template>
      <!-- 列表 -->
      <go-list></go-list>
      <!-- 列表 -->
    </el-main>
    <el-footer class="fooer">
      <span id="busuanzi_container_site_uv">
        承蒙
        <span id="busuanzi_value_site_uv"></span>位朋友
      </span>
      <span id="busuanzi_container_site_pv">
        造访
        <span id="busuanzi_value_site_pv"></span>次
        <span class="post-meta-divider">|</span>
      </span>
      <span>©2018 - {{currentYear}}</span>
      <a aria-label="Homepage" title="感谢空间支持:GitHub" class="footer-octicon mx-lg-4" href="https://github.com">
        <svg height="24" class="octicon" viewBox="0 0 16 16" version="1.1" width="24" aria-hidden="true">
          <path fill-rule="evenodd"
            d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0 0 16 8c0-4.42-3.58-8-8-8z" />
        </svg>
      </a>
      <a href="https://jeonke.itcnw3c.com/icb/itcnw3c.com/#/" id="octicon">
        <span>ICB证DSTGO008号</span>
      </a>
    </el-footer>
  </el-container>
</template>
<script type="text/javascript">
  import logoSelect from "./logo-select.vue";
  import goList from "./go-list.vue";
  import busuanzi from "busuanzi.pure.js";

  export default {
    //注册组件
    components: {
      "logo-select": logoSelect,
      "go-list": goList,
      busuanzi: busuanzi,
    },
    data: () => ({
      time: '',
      times: '',
      date: '',
      isShow: true,
      searchString: "",
      myData: [], //用来接收ajax得到的数据
      now: -1,
      searchIndex: 0,
      errorImg:'this.src="https://raw.githubusercontent.com/Jeonke/PicGoPictureBed/master/img/5-160914192R6-50.gif"',
      logoData: [],
      userList: [
     //   {
      //     name: "Item 1",
      //     category: "Category 1",
      //     href: "https://www.example.com/item1",
      //     title: "Item 1 Title",
      //     image: "https://www.example.com/images/item1.jpg"
      //   }
      ]
    }),
    created() {
      this.handleUserList();
    },
    mounted() {
      this.times = window.setInterval(() => {
        this.updateTime()
      }, 1000);

      // 初始化代码
      var obj = this;
      var acl;
      var url;
      acl = this.$logoData;
      this.$http.get(acl).then(function (res) {
        // console.log(res)
        obj.logoData = res.data;
      })

      url = this.$globaljson;
      this.$http.get(url).then(function (res) {
        // console.log(res)
        // console.log("mounted:" + res.data)
        obj.userList = res.data;
      })
    },
    methods: {
      changeisShow: function () {
        this.isShow = !this.isShow;//取反
      },
      beforeDestroy() {
        clearInterval(this.times);
        this.times = null;
      },
      // 时钟
      updateTime() {
        // let week = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'];
        let week = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];
        let cd = new Date();
        this.time = this.zeroPadding(cd.getHours(), 2) + ':' + this.zeroPadding(cd.getMinutes(), 2) + ':' + this.zeroPadding(cd.getSeconds(), 2);
        this.date = this.zeroPadding(cd.getFullYear(), 4) + '-' + this.zeroPadding(cd.getMonth() + 1, 2) + '-' + this.zeroPadding(cd.getDate(), 2) + ' ' + week[cd.getDay()];
      },
      zeroPadding(num, digit) {
        var zero = '';
        for (var i = 0; i < digit; i++) {
          zero += '0';
        }
        return (zero + num).slice(-digit);
      },
     dleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        // 点击第几页
        // console.log(this.currentPage)
      },     
      // &event是实参，表示event对象
      get: function (ev) {
        // 如果按得键是上或者下，就不进行ajax
        if (ev.keyCode == 38 || ev.keyCode == 40) {
          return;
        }
        this.$http
          .jsonp(
            this.logoData[0].searchSrc +
            this
          )
          .then(function (res) {
            this.myData = res.data.s;
          });
      },
      selectDown: function () {
        this.now++;
        //到达最后一个时，再按下就回到第一个
        if (this.now == this.myData.length) {
          this.now = 0;
        }
        this.searchString = this.myData[this.now];
      },
      selectUp: function () {
        this.now--;
        //同上
        if (this.now == -1) {
          this.now = this.myData.length - 1;
        }
        this.searchString = this.myData[this.now];
      },
      search: function () {
        //打开对应的搜索界面
        window.open(
          this.logoData[this.searchIndex].searchSrc +
          encodeURIComponent(this.searchString)
        );
      },
      selectHover: function (index) {
        this.now = index;
      },
      selectClick: function (index) {
        this.searchString = this.myData[index];
        this.search();
      },
      clearInput: function () {
        this.searchString = "";
        this.$http
          .jsonp(
            this.logoData[0].searchSrc +
            this.searchString
          )
          .then(function (res) {
            this.myData = res.data.s;
          });
      },
      getIndex: function (index) {
        this.searchIndex = index;
      },
    },
    computed: {
      currentYear() {
      return new Date().getFullYear();
    },
      // 计算数学，匹配搜索
      filteredArticles: function () {
        var articles_array = this.userList,
          searchString = this.searchString;

        if (!searchString) {
          return articles_array;
        }

        searchString = searchString.trim().toLowerCase();

        articles_array = articles_array.filter(function (item) {
          if (
            item.title.toLowerCase().indexOf(searchString) !== -1 &&
            searchString !== ""
          ) {
            return item;
          }
        });

        // 返回过来后的数组
        return articles_array;
      },
    },
  };
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  a {
    font-size: 1em;
    color: transparent;
    background-image: -webkit-linear-gradient(left,
        red,
        orange 40%,
        rgba(9, 253, 10, 0.88) 75%,
        #2aff30 100%);
    -webkit-background-clip: text;
    background-size: 200% 100%;
    animation: vary 2s infinite ease-in-out;
  }

  @keyframes vary {
    0% {
      background-position: 0;
    }

    100% {
      background-position: -100%;
    }
  }

  a {
    color: transparent;
    cursor: pointer;
    padding: 10px 10px 10px 10px;
    font-size: 16px;
    line-height: 26px;
    text-decoration: none;
  }

  .el-link.el-link--default {
    color: transparent;
  }

  .el-link.el-link--default:hover {
    color: #409eff;
  }

  /*布局容器*/

  .el-header,
  .el-footer {
    text-align: center;
    line-height: 40px;
  }

  .el-main {
    text-align: center;
    line-height: 70px;
  }

  .el-aside {
    text-align: center;
    line-height: 200px;
  }

  body .mains>>>.el-pagination__total {
    margin-right: 0.133333rem;
    font-weight: normal;
    color: #fff;
  }

  body .mains>>>.el-pagination__jump {
    margin-left: 0.32rem;
    font-weight: normal;
    color: #fff;
  }

  body>.el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }

  .ishinner {
    position: absolute;
    right: 0;
    font-weight: normal;
    color: #fff;
  }
</style>
<!--搜索输入框-->
<style scoped>
  [v-cloak] {
    display: none;
  }

  * {
    margin: 0;
    padding: 0;
  }

  body {
    font: 18px/1.3 "Open Sans", sans-serif;
    color: #5e5b64;
    text-align: center;
  }

  a,
  a:visited {
    outline: none;
    color: #389dc1;
  }

  a:hover {
    text-decoration: none;
  }

  section,
  footer,
  header,
  aside,
  nav {
    display: block;
  }

  /*-------------------------
      搜索输入框
  --------------------------*/

  ul {
    list-style: none;
    width: 528px;
    margin: 0 auto;
    text-align: left;
  }

  ul li {
    border-bottom: 2px solid #ddd;   /*no*/
    border-radius: 6px;
    padding: 10px;
    overflow: hidden;
  }

  ul li img {
    width: 60px;
    height: 60px;
    float: left;
    border: none;
  }

  .search-input ul li p {    
    margin-left: 75px;
    font-weight: bold;
    padding-top: 12px;
    color: #6e7a7f;
  }

  .search-input {
    font-size: 18px; /*px*/
    vertical-align: top;
    outline: 0;
    box-shadow: none;
    border-radius: 12px 12px 12px 12px;
    border: 1px solid #c4c7ce; /*no*/
    background: #fff;
    color: #222;
    overflow: hidden;

    height: 45px;
    width: 675px;
    margin: 0 auto;
    margin-top: 10px;
    position: relative;
  }

  .search-input input {
    background: #fff no-repeat 13px 13px;
    background-image: url(data:image/png;base64,);
    border: none;
    width: 100%;
    line-height: 19px;
    border-radius: 6px 0 0 6px;
    box-shadow: 0 2px 8px #c4c4c4 inset;
    text-align: left;
    font: 16px/18px arial;  /*px*/
    outline: none;
    text-indent: 40px;
    box-sizing: border-box;
    width: 575px;
    height: 50px;
    font-size: 42px;   /*px*/
    float: left;
    padding-left: 10px;
    padding-right: 10px;
    overflow: hidden;
  }

  .search-btn {
    border: 1px solid #4673b3;  /*no*/
    background-color: #416eb3;
    color: white;
    width: 100px;
    height: 49px;
    line-height: 50px;
    line-height: 44px\9;
    background-color: #4e6ef2;
    border-radius: 0 10px 10px 0;
    font-size: 17px;
    box-shadow: none;
    font-weight: 400;
    border: 0;
    outline: 0;
    letter-spacing: normal;
    float: left;
  }

  .search-btn {
    cursor: pointer;
  }

  .search-select {
    position: absolute;
    top: 45px;
    width: 500px;
    box-sizing: border-box;
    z-index: 999;
  }

  .search-select li {
    border: 1px solid #d4d4d4;    /*no*/
    border-top: none;
    border-bottom: none;
    background-color: #fff;
    width: 100%;
  }

  .search-select-option {
    box-sizing: border-box;
    padding: 7px 10px;
  }

  .selectback {
    background-color: #eee !important;
    cursor: pointer;
  }

  input::-ms-clear {
    display: none;
  }

  .search-reset {
    outline: none;
    color: #5e5b64;
    width: 21px;
    height: 21px;
    position: absolute;
    display: block;
    line-height: 21px;
    text-align: center;
    cursor: pointer;
    font-size: 20px;
    right: 110px;
    top: 12px;
  }

  .logo ul li {
    list-style-type: disc;
    margin: 8px 0 0 32px;
  }

  .logo p {
    font-size: 18px;
    color: #4d4d4d;
    font-weight: bold;
    line-height: 26px;
    margin: -4px 0px 0px 75px;
  }

  .logo img {
    width: 50px;
    height: 50px;
    background-color: #f8f5f5f1;
    border-radius: 6px;
    border: 1px solid rgba(0, 0, 0, 0.05);
  }

  .search-select-list {
    transition: all 0.5s;
  }

  .itemfade-enter,
  .itemfade-leave-active {
    opacity: 0;
  }

  .itemfade-leave-active {
    position: absolute;
  }

  .selectback {
    background-color: #eee !important;
    cursor: pointer;
  }

  .search-select ul {
    margin: 0;
    text-align: left;
  }

  .logo {
    position: absolute;
    z-index: 99999;
    border: 1px solid #c4c7ce;/*no*/
    border-radius: 0 0 10px 10px;
    border-top: 0;
    border-bottom: 0;
    overflow: visible;
    left: 50%;
    margin-left: -295px;
    background-color: white;
  }

  .fooer {
    width: 100%;
    position: fixed;
    z-index: 100;
    bottom: 0px;
    left: 0px;
    overflow: hidden;
    zoom: 1;
    margin: 0px;
    line-height: 40px;
    padding: 50px;
    color: #fff;
    background-image: linear-gradient(#52ec0700, #4dbf163b, #02ad02e3);
  }

  .octicon {
    border-radius: 50%;
  }

  #octicon {
    float: right;
    color: #fff;
  }

  /* 时钟 */
  p {
    margin: 0;
    padding: 0;
  }

  #clock {
    font-family: 'Share Tech Mono', monospace;
    text-align: center;
    position: absolute;
    bottom: 12%;
    left: 1%;
    color: #daf6ff;
    background-color: #0606069d;
    border: 1px solid rgba(0, 0, 0, 0.05);
    border-radius: 12px;
    text-shadow: 0 0 20px #0aafe6, 0 0 20px rgba(10, 175, 230, 0);
  }

  #clock {
    letter-spacing: 0.05em;
    font-size: 80px;
    padding: 5px 0;
  }

  #clock .date {
    letter-spacing: 0.1em;
    font-size: 24px;
  }

  #clock .text {
    letter-spacing: 0.1em;
    font-size: 12px;
    padding: 20px 0 0;
  }
</style>


