<template>
   <el-main class="mains">
    <div class="navigation-page">
      <div class="category-filter">
        <el-select v-model="selectedCategory" placeholder="请选择" :value="selectedCategory">
            <!-- <el-option key="全部" label="全部" value=""></el-option> -->
            <el-option v-for="(item, index) in categories" :key="index" :label="item" :value="item"></el-option>
        </el-select>
      </div>
      <div class="navigation-grid">
        <div class="navigation-item" v-for="(user, index) in paginatedUsers" :key="index">
            <el-link :href="user.href" :title="user.title" target="_blank" :underline="false">
              <img class="navigation-icon" v-bind:src="user.image" :onerror="errorImg" />
              <p class="navigation-title">{{ user.title }}</p>
            </el-link>
        </div>
      </div>
      <div class="pagination">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="currentPage" :page-size="pagesize" layout="total,prev,pager,next,jumper"
          :total="totalPages"></el-pagination>
     </div>
    </div>
   </el-main>
  </template>
  
  <script>
  export default {
    data() {
      return {
        userList: [
          {
            name: "",
            category: "",
            href: "",
            title: "",
            image: ""
          },
          // Add more users as needed
        ],
        errorImg:'this.src="https://raw.githubusercontent.com/Jeonke/PicGoPictureBed/master/img/5-160914192R6-50.gif"',
        currentPage: 1,
        pagesize: 18,
        selectedCategory:'全部',
      };
    },
    computed: {
      categories() {
        const categories = [...new Set(this.userList.map(user => user.category))];
        // 使用随机排序
        return ['全部', ...categories];
      },
      filteredUsers() {
        if (this.selectedCategory === "全部") {
          if(this.$userListsort){
            return this.userList.sort(() => Math.random() - 0.5);
          }else{
            return this.userList;
          }
        } else {
          if(this.$userListsort){
            return this.userList.filter(user => user.category === this.selectedCategory).sort(() => Math.random() - 0.5);
          }else{
            return this.userList.filter(user => user.category === this.selectedCategory);
          }
        }
      },
      totalPages() {
        return Math.ceil(this.filteredUsers.length);
      },
      paginatedUsers() {
        const startIndex = (this.currentPage - 1) * this.pagesize;
        const endIndex = startIndex + this.pagesize;
        return this.filteredUsers.slice(startIndex, endIndex);
      }
    },
    created() {
        this.handleUserList();
    },
    methods: {
     handleUserList() {
            var obj = this;
            var url = this.$globaljson
            this.$http.get(url).then(function (res) {
                // console.log("handleUserList:" + res.data)
                obj.userList = res.data;
            })
            // console.log(this.$globaljson);
     },          
     // 初始页currentPage、初始每页数据数pagesize和数据data
     handleSizeChange: function (size) {
          this.pagesize = size;
          // 每页下拉显示数据
          // console.log(this.pagesize)
        },
     handleCurrentChange: function (currentPage) {
          this.currentPage = currentPage;
          // 点击第几页
          // console.log(this.currentPage)
      }
    }
  };
  </script>

  <style scoped>
  .mains {
    z-index: 200;
    margin-top: 10px;
  }

  .el-main{
    text-align: center;
    line-height: 20px;
  }

  .navigation-page {
    width: 50%; /* 或者任何你想要的固定宽度 */
    margin: 0 auto; /* 居中对齐 */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }

  .category-filter {
    margin-bottom: 10px;
  }
  
  .navigation-grid {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 20px;
    margin-top: 10px;
  }
  
  .navigation-item {
    text-align: center;
  }
  
  .navigation-icon {
    width: 60px;
    height: 60px;
    background-color: #f8f5f5f1;
    border-radius: 6px;
    border: 1px solid rgba(0, 0, 0, 0.05);
  }
  
  .navigation-title {
    margin-top: 10px;
    max-height: 40px;
    max-width: 80px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .navigation-title::after {
      content: "...";
      background: -webkit-linear-gradient(left, transparent, rgb(14, 243, 52) 55%);
      background: -o-linear-gradient(right, transparent, rgba(24, 223, 107, 0.959) 55%);
      background: -moz-linear-gradient(right, transparent, rgb(12, 247, 12) 55%);
      background: linear-gradient(to right, transparent, rgba(72, 247, 3, 0.658) 55%);
    }
  .navigation-title {
    font-size: 16px;
    color: transparent;
    background-image: -webkit-linear-gradient(left,
          red,
          orange 40%,
          rgba(9, 253, 10, 0.88) 75%,
          #2aff30 100%);
      -webkit-background-clip: text;
      background-size: 200% 100%;
      animation: vary 2s infinite ease-in-out;
    }
  
    @keyframes vary {
      0% {
        background-position: 0;
      }
  
      100% {
        background-position: -100%;
      }
    }
  
  .pagination {
    margin-top: 20px;
  }
  
  .pagination-button {
    margin: 0 5px;
    padding: 5px 10px;
    cursor: pointer;
  }
  </style>
  


