// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'lib-flexible/flexible'

Vue.config.productionTip = true //busuanzi统计

Vue.prototype.$userListsort = false //是否随机排序
Vue.prototype.$globaljson = '/static/json/userList.json';//导航书签数据
Vue.prototype.$logoData = '/static/json/logoData.json';//搜索引擎数据

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



