import axios from 'axios'
//获取歌词
export const getWords = (id) => {
  return axios.get(`https://caicaimusicapi.herokuapp.com/lyric?id=${id}&timestamp=1503019930000`);
}
//获取歌曲详情
export const getMusicInfo = (id) => {
  return axios.get(`https://caicaimusicapi.herokuapp.com/song/detail?ids=${id}&timestamp=1503019930000`);
}
//获取歌曲url
export const getMusicUrl = (id) => {
  return axios.get(`https://caicaimusicapi.herokuapp.com/song/url?id=${id}&timestamp=1503019930000`);
}
//获取热门歌曲
export const getHotMusic = (id) => {
  return axios.get(`https://caicaimusicapi.herokuapp.com/playlist/detail?id=${id}&timestamp=1503019930000`);
}
//获取搜索建议
export const getSearchSuggest = (key) => {
  return axios.get(`https://caicaimusicapi.herokuapp.com/search/suggest?keywords=${key}&timestamp=1503019930000`)
}
//获取歌曲热门评论
export const getHotTalk = (id) => {
  return axios.get(`https://caicaimusicapi.herokuapp.com/comment/music?id=${id}&limit=3&timestamp=1503019930000`)
}

