<template>
  <!-- 选择logo组件 -->
  <div class="main">
    <div class="main-logo">
      <img id="lopti" :src="logoData[selectedNow].imgSrc" @click="toggleFlag">     
      <span class="logoList-arrow" @click="toggleFlag"></span>
      <!-- tranition是vue的过渡效果 -->
      <transition name="logofade">
        <ul v-show="logoListFlag&&mouseLeaveFlag" class="logoList" @mouseleave="mouseLeaveList">
          <li v-for="(item,index) in logoData" class="logoItem" @mouseover="logoListHover(index)"
            :class="{selectback:index==logoNow}" @click="logoSelected(index)" :key="index">
            <img :src="item.imgSrc" >
          </li>
        </ul>
      </transition>
    </div>
    <span id="h" style="position: absolute; z-index:-1; top: 33px;right: 107px;">
       <img id="onpti" :src="backgourndStyle" :onerror="errorImg" />      
       <i id="kcles">๑҉</i>
    </span>
    <div class="four">
      <!-- <weather class="weather" size="normal" type="oneline" color="ffffff">
      </weather> -->
    </div>
  </div>

</template>

<script type="text/javascript">

  // import weather from "../components/weather/packages/vWeather/src/VWeather";

  // export defult是es6中的方法，将整个花括号对象暴露出去;
  export default {
    // components: {
    //   'Weather': weather
    // },
    data: function () {
      return {
        errorImg:'this.src="https://raw.githubusercontent.com/Jeonke/PicGoPictureBed/master/img/avatar.gif"',
        selectedNow: 0,
        logoNow: -1,
        logoListFlag: false,
        mouseLeaveFlag: false,
        logoData: [
          // {
          //   imgSrc: require('../assets/baidu_logo.png')
          // }, {
          //   imgSrc: require('../assets/360_logo.png')
          // }, {
          //   imgSrc: require('../assets/bingying_logo.png')
          // }, {
          //   imgSrc: require('../assets/sougou_logo.png')
          // }
        ]
      }
    },
    computed: {
      backgourndStyle: function () {
        let imgName = Math.floor(Math.random() * 26); //进行计算随机
        if (imgName < 10) {
          imgName = "0" + imgName;
        }
        let style =
          "https://raw.githubusercontent.com/Jeonke/PicGoPictureBed/master/img/Sitich/Sitich" +
          imgName +
          ".gif";
        return style;
      }
    },
    created() {
       this.handlelogoDataList();
    },
    methods: {
      toggleFlag: function () {
        this.logoListFlag = !this.logoListFlag;
        this.mouseLeaveFlag = true;
      },
      logoListHover: function (index) {
        this.logoNow = index;
      },
      logoSelected: function (index) {
        this.selectedNow = index;
        this.logoListFlag = false;
        // 触发父组件的自定义事件，向父组件传参数,selectNow是选择了哪个搜索引擎的索引，父组件得到了之后就可以指定搜索时跳转到哪个搜索引擎
        this.$emit('getindex', this.selectedNow);
      },
      mouseLeaveList: function () {
        //鼠标离开列表事件
        this.mouseLeaveFlag = false;
        this.logoListFlag = false;
      },
      handlelogoDataList() {
            var obj = this;
            var url = this.$logoData
            this.$http.get(url).then(function (res) {
                // console.log("handlelogoDataList:" + res.data)
                obj.logoData = res.data;
            })
            // console.log(this.$logoData);
     },    
    }
  }
</script>

<style type="text/css">
  ul {
    list-style: none;
    padding: 0;
    margin: 0
  }

  .main-logo {
    /*width: 600px;*/
    height: 140px;
    position: relative;
  }

  #onpti {
    display: block;
    margin: 0 auto;
    background-repeat: no-repeat;
    width:60px;
    height:60px;
    padding: 18px;
    right: calc(50% - 5px/2);
    position: absolute;
  }

  #lopti {
    display: block;
    margin: 0 auto;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
    background-repeat: no-repeat;
    margin-top: 8px;
    height: 140px;
    left: calc(50% - 482px/2);
    position: absolute;
  }

  #h i {
    font-family: 隶书;  /*px*/
    font-size: 170px; /*px*/
    font-style: normal;
  }
  
  #kcles {
    -webkit-text-fill-color: transparent;
    background: linear-gradient(to right, rgb(255, 230, 0), hsl(44, 100%, 50%));
    background-image: -webkit-linear-gradient(bottom, hsl(4, 100%, 50%), hsl(44, 100%, 50%), rgb(255, 230, 0));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-animation: hue 60s infinite linear;
  }

  @-webkit-keyframes hue {
    from {
      -webkit-filter: hue-rotate(0deg);
    }

    to {
      -webkit-filter: hue-rotate(-360deg);
    }
  }

  .main-logo img {
    display: block;
    margin: 0 auto;
    user-select: none;
    cursor: pointer;
  }

  .logoList-arrow {
    position: absolute;
    width: 0;
    height: 0;
    /*border: 8px solid;*/
    /*border-color: #000 transparent transparent transparent;*/
    right: 100px;
    top: 66px;
    cursor: pointer
  }

  .logoList {
    position: absolute;
    top: 100%;
    width: 200px;
    left: 50%;
    margin-left: -100px;
    z-index: 999999;
    border: 1px solid #d4d4d4; /*no*/
  }

  .logoList li {
    width: 100%;
    height: 80px;
    background-color: #FEFEFE;
    line-height: 80px;
    padding-top: 1px;
  }

  .logoList li img {
    width: 100%;
    margin-top: 10px;
  }

  .logofade-enter-active,
  .logofade-leave-active {
    transition: all .5s;
  }

  .logofade-enter,
  .logofade-leave-active {
    opacity: 0;
    transform: translateY(20px);
  }

  .selectback {
    background-color: #eee !important;
    cursor: pointer
  }

  .four .weather {
    background: #000000;
    border-radius: 22px;
    box-shadow: 1px 1px 10px #00000022;
  }
</style>



